<script>
import CountDown from '@/components/CountDown';
export default {
	//数据集合
	data() {
		return {
			tablename: 'dianyingpingfen',
			baseUrl: '',
			breadcrumbItem: [
				{
					name: 'Details'
				}
			],
			title: '',
			detailBanner: [],
			endTime: 0,
			detail: {},
			activeName: 'first',
			total: 1,
			pageSize: 5,
			pageSizes: [10, 20, 30, 50],
			totalPage: 1,
			rules: {
				content: [
					{ required: true, message: 'Please enter the content', trigger: 'blur' }
				]
			},
			buynumber: 1,
		}
	},
	created() {
		this.init();
	},
	//方法集合
	methods: {
		init() {
			this.baseUrl = this.$config.baseUrl;
			if (this.$route.query.detailObj) {
				this.detail = JSON.parse(this.$route.query.detailObj);
			}
			this.$http.get(this.tablename + '/detail/' + this.detail.id, {}).then(res => {
				if (res.data.code == 0) {
					this.detail = res.data.data;
					this.title = this.detail.dianyingmingcheng;
					this.detailBanner = this.detail.haibao ? this.detail.haibao.split(",") : [];
					this.$forceUpdate();
				}
			});


		},
		curChange(page) {
			this.getDiscussList(page);
		},
		prevClick(page) {
			this.getDiscussList(page);
		},
		nextClick(page) {
			this.getDiscussList(page);
		},
		// 下载
		download(file) {
			if (!file) {
				this.$message({
					type: 'error',
					message: '文件不存在',
					duration: 1500,
				});
				return;
			}
			window.open(this.baseUrl + file)
		},


	},
	components: {
		CountDown
	}
}
</script>

